:root {
  --color-background-primary: white;
  --color-background-secondary: #949b9f48;
  --color-surface-dark: #333;
  --color-text-primary: #222;
  --color-text-on-dark: white;
  --color-accent: #007acc;
  --color-border: #ccc;
  --color-input-bg: white;
  --color-input-text: #222;
}

@media (prefers-color-scheme: dark) {
  :root {
    --color-background-primary: #222;
    --color-background-secondary: #444;
    --color-surface-dark: #111;
    --color-text-primary: #eee;
    --color-text-on-dark: #eee;
    --color-border: #555;
    --color-input-bg: #333;
    --color-input-text: #eee;
  }
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

body {
  background-color: var(--color-background-primary);
  color: var(--color-text-primary);
}

nav {
  background-color: var(--color-surface-dark);
  color: var(--color-text-on-dark);
  text-align: center;
  padding: 10px 20px;
}

nav .logo {
  height: 30px;
  font-size: 24px;
  font-weight: bold;
  display: block;
}

nav .nav-links a {
  color: var(--color-text-on-dark);
  margin: 0 15px;
  font-size: 16px;
  text-decoration: none;
}

header {
  text-align: center;
  margin-bottom: 40px;
}

header img {
  width: 100%;
  height: auto;
}

footer {
  background-color: var(--color-surface-dark);
  color: var(--color-text-on-dark);
  text-align: center;
  margin-top: 40px;
  padding: 10px 0;
}

#contact {
  background-color: var(--color-background-secondary);
  width: 70%;
}

h1, h2 {
  color: var(--color-accent);
  margin-top: 10px;
  margin-bottom: 20px;
}

p {
  margin-bottom: 20px;
  line-height: 1.6;
}

input, input[type="text"], input[type="email"], textarea {
  border: 1px solid #ccc;
  border-radius: 4px;
  width: 100%;
  margin: 10px 0;
  padding: 10px;
}

button, button[type="submit"] {
  color: #fff;
  cursor: pointer;
  background-color: #007acc;
  border: none;
  border-radius: 4px;
  padding: 10px 20px;
}

section {
  width: 90%;
  margin: auto auto 40px;
  padding: 20px;
  display: block;
}

.intro-section {
  align-items: center;
  display: flex;
}

.intro-text {
  width: 50%;
  padding: 20px;
}

.intro-image {
  width: 50%;
}

.intro-image img {
  width: 100%;
  height: auto;
  display: block;
}

.form-group {
  margin-bottom: 20px;
}

@media screen and (width <= 768px) {
  #contact {
    width: 90%;
  }

  nav .logo {
    height: 40px;
  }

  .intro-section {
    flex-direction: column;
  }

  .intro-image, .intro-text {
    width: 100%;
  }
}
